<template>
  <v-data-table
    :headers="headers"
    :items="products"
    :footer-props="{ 'items-per-page-text': 'Produtos por página' }"
    sort-by="name"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Produtos Eletrônicos</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>

        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              Novo produto
            </v-btn>
          </template>

          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="6">
                    <v-text-field
                      v-model="editedItem.name"
                      label="Nome"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-text-field
                      v-model="editedItem.manufacture"
                      label="Fabricante"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-text-field
                      v-model.number="editedItem.amount"
                      label="Quantidade"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6">
                    <v-text-field
                      v-model.number="editedItem.price"
                      label="Preço"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
              <v-btn color="blue darken-1" text @click="save">Salvar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title>Confirma a remoção do item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancelar</v-btn
              >
              <v-btn color="red darken-1" text @click="removeDB">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
      <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="loadDB">Recarregar</v-btn>
    </template>
  </v-data-table>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      { text: "Nome", align: "start", value: "name", sortable: true },
      { text: "Fabricante", value: "manufacture", sortable: false },
      { text: "Quantidade", value: "amount", sortable: true },
      { text: "Preço", value: "price", sortable: true },
      { text: "Ações", value: "actions", sortable: false },
    ],
    products: [],
    editedIndex: -1,
    editedItem: {
      id: "",
      name: "",
      manufacture: "",
      amount: 0,
      price: 0,
    },
    defaultItem: {
      id: "",
      name: "",
      manufacture: "",
      amount: 0,
      price: 0,
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Adicionar Produto" : "Alterar Produto";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.loadDB();
  },

  methods: {
    loadDB() {
      // Retrieve
      this.$http.get("products.json").then((res) => {
        this.products = res.data;
      });
    },
    removeDB() {
      // Delete
      let id = this.editedItem.id;
      this.$http.delete(`/products/${id}.json`).then(() => {
        this.products.splice(this.editedIndex, 1);
        this.closeDelete();
      });
    },
    addDB() {
      // Create
      this.$http.post("products.json", this.editedItem).then((res) => {
        let key = res.request.responseText.trim();
        key = key
          .replace("{", "")
          .replace("}", "")
          .replace(":", "")
          .replace("name", "")
          .replace(/\"/g, "");
        this.products[this.products.length - 1].id = key;
      }, this.products.push(this.editedItem));
    },
    updateDB() {
      // Update
      this.$http.put("products/"+this.editedItem.id+".json", this.editedItem).then(
        Object.assign(this.products[this.editedIndex], this.editedItem)
      );
    },
    editItem(item) {
      this.editedIndex = this.products.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.products.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        this.updateDB();
      } else {
        this.addDB();
      }
      this.close();
    },
  },
};
</script>