import Vue from "vue";
import Axios from "axios";

Axios.defaults.baseURL = 'https://ivanpires-default-rtdb.firebaseio.com/'

Vue.use({
    install(Vue) {
        Vue.prototype.$http = Axios
    }
})


Vue.prototype.$http.interceptors.response.use(res => {
    const array = []
    for (let chave in res.data) {
      array.push({
        id: chave,
        name: res.data[chave].name,
        manufacture: res.data[chave].manufacture,      
        amount: res.data[chave].amount,      
        price: res.data[chave].price,      
      })
    }
    res.data = array
    return res
  
  // array.push({id: chave, ...res.data[chave]}) // spread 
  
  })