<template>
  <v-card>
    <v-card-title>Entre em Contato</v-card-title>
    <v-card-subtitle
      >Utilize este formulário para contactar com administrador do
      sistema</v-card-subtitle
    >
    <v-card-text>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-text-field
          sv-model="name"
          :counter="10"
          :rules="nameRules"
          label="Seu nome"
          required
        ></v-text-field>

        <v-text-field
          v-model="email"
          :rules="emailRules"
          label="Seu e-mail"
          required
        ></v-text-field>

        <v-textarea class="mb-7"
          v-model="msg"
          :rules="msgRules"
          clearable
          clear-icon="mdi-close-circle"
          label="Escreva sua mensagem"
          required
        ></v-textarea>

        <v-btn color="success" class="mr-4" @click="validate">
          Enviar mensagem
        </v-btn>
      </v-form>
    </v-card-text>
  </v-card>
</template>

<script>
  export default {
    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Nome é obrigatório',
        v => (v && v.length <= 10) || 'Nome deve ter ao menos 10 caracteres',
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail é obrigatório',
        v => /.+@.+\..+/.test(v) || 'E-mail deve ser válido',
      ],
      msg: '',
      msgRules: [
        v => !!v || 'Mensagem é obrigatória',
      ],     
    }),

    methods: {
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
    },
  }
</script>

